<template>
  <div>
    <div class="player">
      <span class="title">
        Josh's Playlist
      </span><br><br><br>
      <div class="btns">
        <button v-if="!isPlaying" @click="play">Play</button>
        <button v-if="isPlaying" @click="pause">Pause</button>
        <button @click="prev">Previous</button>
        <button @click="next">Next</button>
      </div><br><br><br>
      <div class="playing">
        <label>Song Title: </label><h3 class="currentTitle">{{ songs[index].title }}</h3>
        <br><br>
        <label>Song Author: </label><h3 class="currentAuthor">{{ songs[index].author }}</h3>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "FrontPage",
  data() {
    return {
      autoplay: false,
      isPlaying: false,
      index: 0,
      songs: [
        {
          title: "Cradles",
          author: "Sub Urban",
          src: require("../assets/cradles.mp3"),
        },
        {
          title: "Something For your M.I.N.D",
          author: "SuperOrganism",
          src: require("../assets/mind.mp3"),
        },
        {
            title: "Blame It on the Kids",
            author: "AVIVA",
            src: require("../assets/onk.mp3"),
        },
        {
            title: "Alien Boy",
            author: "Oliver Tree",
            src: require("../assets/alien.mp3"),
        },
        {
            title: "Rock Bottom",
            author: "grandson",
            src: require("../assets/rock.mp3"),
        }
        
      ],
    playing: "",

    };
  },
  methods: {
    play() {
        if(this.index > this.songs.length -1){
            this.index = 0;
        }
        if(this.playing == ""){
            this.playing = new Audio(this.songs[this.index].src);
        }
        if(this.playing.play() != undefined){
            this.playing.play();
        }
        this.playing.onended = function(){
            this.isPlaying = false;
            if(this.autoplay == true){
                this.next();
            }  
        }
        this.isPlaying = true;
    },
    pause (){
        this.playing.pause();
        this.isPlaying = false;
    },

    next (){
        this.index++;
        this.playing.pause();
        this.playing = "";
        this.play();
        this.playing.play();
    },
    prev (){
        this.index--;
        this.playing.pause();
        this.playing = "";
        this.play();
        this.playing.play();
    }
  }
};
</script>
